/* Colors */
/* Colors */
@import url("https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap");
* {
  box-sizing: border-box;
}

body {
  position: relative;
  font-family: "DM Sans", sans-serif;
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
}
body h1, body h2, body h3, body h4, body h5, body h6, body p, body ul, body ol, body li, body form, body label, body button, body input,
body textarea, body select, body option, body hr, body figure, body figcaption, body blockquote, body pre,
body code, body table, body td, body th, body tr, body div, body span, body a, body img, body video, body svg, body path, body i, body b,
body strong, body em, body small, body time, body mark, body section, body article, body aside, body header, body nav,
body footer, body main, body address, body video, body audio, body canvas, body details, body summary, body input {
  margin: 0;
  padding: 0;
}

.cta {
  cursor: pointer;
  text-decoration: none;
  color: #FFFFFF;
  background-color: #901C1C;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: 500;
}
.cta:hover {
  background-color: #D3573C;
}

/* Colors */
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 43px;
  margin-left: 100px;
  margin-right: 100px;
}

h1 {
  color: #D3573C;
  margin-right: 100px;
}

#logo {
  width: 200px;
}

/* Colors */
.photographersGrid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 70px;
  margin-top: 100px;
}
.photographer_link {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  color: unset;
}
.photographer_portrait {
  width: 200px;
  height: 200px;
}
.photographer_portrait_wrapper {
  position: relative;
  height: 200px;
  width: 200px;
}
.photographer_portrait_background {
  background-size: 200px;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.photographer_portrait_blurFilter {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0) 0.1%, rgba(250, 250, 250, 0.97) 65%, rgb(250, 250, 250) 100%);
}
.photographer_portrait_overlay {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 185px;
  width: 185px;
  border-radius: 50%;
  overflow: hidden;
  object-position: center;
}
.photographer_description {
  text-align: center;
}
.photographer_name {
  color: #D3573C;
  font-size: 36px;
  line-height: 83.33px;
  margin: 0;
}
.photographer_location {
  color: #901C1C;
  font-size: 24px;
  line-height: 31.25px;
  margin-top: 0;
  margin-bottom: 19px;
}
.photographer_tagline {
  color: #525252;
  font-size: 18px;
  line-height: 23.44px;
  margin: 0;
}
.photographer_card {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: left;
  height: 300px;
  padding-top: 71px;
  padding-bottom: 42px;
}
.photographer_banner {
  display: flex;
  background-color: #FAFAFA;
  justify-content: space-between;
  align-items: center;
  height: 313px;
  margin: 40px 100px 20px 100px;
  padding: 50px 57px;
}
.photographer_banner_portrait_blurFilter {
  background: radial-gradient(circle, rgba(255, 255, 255, 0) 0.1%, rgba(255, 255, 255, 0.97) 65%, rgb(255, 255, 255) 100%);
}

/* Colors */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Colors */
#contact {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
#contact .modal {
  background-color: #DB8876;
  border-radius: 5px;
}
#contact .form_contact {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 20px;
  padding: 0 35px 38px 35px;
}
#contact .form_contact_header {
  display: flex;
  align-items: flex-start;
  margin: 15px 35px;
  font-size: unset;
}
#contact .form_contact_header_title {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding-left: 35px;
  padding-top: 15px;
  margin-bottom: 20px;
  width: 450px;
}
#contact .form_contact_header_title h2, #contact .form_contact_header_title p {
  font-size: 64px;
  font-weight: 400;
  color: #000000;
  text-align: left;
  white-space: nowrap;
}
#contact .form_contact label {
  color: #312E2E;
  font-size: 36px;
}
#contact .form_contact div {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: self-start;
}
#contact .form_contact input {
  width: 600px;
  height: 68px;
  border: none;
  border-radius: 5px;
}
#contact .form_contact textarea {
  width: 100%;
  height: 170px;
}
#contact .form_contact .contact_button {
  margin-top: 20px;
}

#closeContactModal {
  cursor: pointer;
  font-size: 42px;
  color: #FFFFFF;
}
#closeContactModal:hover {
  color: #000000;
}

/* Colors */
#lightbox {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
#lightbox .modal {
  display: flex;
  flex-direction: row;
  justify-content: fl;
  background-color: #FFFFFF;
  border-radius: 5px;
  padding-top: 10px;
}
#lightbox .modal figure {
  display: flex;
  flex-direction: column;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 0;
}
#lightbox .modal figure .media_wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80vw;
  height: 80vh;
}
#lightbox .modal figure figcaption {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #901C1C;
  font-size: 24px;
}

.nav-lightbox {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 24px;
}
.nav-lightbox[id=go_to_next_media] {
  position: relative;
}
.nav-lightbox[id=go_to_next_media] .fa.fa-times {
  display: inline-block;
  position: absolute;
  width: 42px;
  height: 42px;
  color: #901C1C;
  top: 45px;
  left: 45px;
  transform: translate(-50%, -50%);
}
.nav-lightbox i {
  display: inline-block;
  cursor: pointer;
  color: #901C1C;
  font-size: 24px;
}
.nav-lightbox i:hover {
  color: #D3573C;
}

/* Colors */
#sort {
  position: relative;
  margin-left: 100px;
}
#sort p {
  padding-right: 20px;
  padding-top: 10px;
  font-size: 18px;
  font-weight: 700;
}
#sort #sortList {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 80px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  cursor: pointer;
  background-color: #901C1C;
  border-radius: 5px;
}
#sort #sortList #arrow {
  cursor: pointer;
  position: absolute;
  top: 23px;
  right: 15px;
  color: #FFFFFF;
}
#sort #sortList div[role=tab] {
  display: none;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  color: #FFFFFF;
  font-size: 18px;
  font-weight: 700;
  width: 170px;
  list-style-type: none;
  padding-left: 20px;
  padding-top: 14px;
  padding-bottom: 14px;
  overflow: hidden;
}
#sort #sortList div[role=tab].active {
  display: block;
}
#sort #sortList div[role=tab]:first-of-type {
  padding-top: 23px;
}
#sort #sortList div[role=tab]:first-of-type:hover {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
#sort #sortList div[role=tab]:last-of-type:hover {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
#sort #sortList div[role=tab]:hover, #sort #sortList div[role=tab]:focus {
  background-color: #DB8876;
}
#sort #sortList hr {
  display: inline-block;
  width: 150px;
  margin: 0;
  margin-left: 10px;
  color: #FFFFFF;
}

/* Colors */
#gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  row-gap: 20px;
  column-gap: 95px;
  margin: 50px 100px;
}

.media {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
  border: none;
}
.media_card {
  display: block;
}
.media_card figure {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 300px;
  margin: 0;
  overflow: hidden;
}
.media_description {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 50px;
}
.media_title, .media_likes {
  color: #901C1C;
  font-size: 18px;
  line-height: 31.25px;
  margin: 0;
}
.media_likes i {
  width: 18px;
  height: 18px;
  font-size: 18px;
}
.media_likes i:hover {
  cursor: pointer;
  color: #D3573C;
}

/* Colors */
#footNote {
  position: sticky;
  bottom: 0;
  left: calc(100% - 376px - 30px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #DB8876;
  font-size: 24px;
  font-weight: 500;
  width: 376px;
  height: 68px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding-left: 31px;
  padding-right: 34px;
}